.header-menu input + label {
  position: fixed;
  top: 20px;
  right: 30px;
  height: 20px;
  width: 20px;
  z-index: 5; }
  .header-menu input + label span {
    position: absolute;
    width: 100%;
    height: 2px;
    top: 50%;
    margin-top: -1px;
    left: 0;
    display: block;
    background: #00ffff;
    -webkit-transition: .5s;
    transition: .5s; }
  .header-menu input + label span:first-child {
    top: 3px; }
  .header-menu input + label span:last-child {
    top: 16px; }

.header-menu label:hover {
  cursor: pointer; }

.header-menu input:checked + label span {
  opacity: 0;
  top: 50%; }

.header-menu input:checked + label span:first-child {
  opacity: 1;
  -webkit-transform: rotate(405deg);
          transform: rotate(405deg); }

.header-menu input:checked + label span:last-child {
  opacity: 1;
  -webkit-transform: rotate(-405deg);
          transform: rotate(-405deg); }

.header-menu input ~ nav {
  background: transparent;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  z-index: 3;
  -webkit-transition: .5s;
  transition: .5s;
  -webkit-transition-delay: .5s;
          transition-delay: .5s;
  overflow: hidden; }
  .header-menu input ~ nav > ul {
    display: flex;
    flex-direction: column;
    width: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    padding-top: 50px; }
    .header-menu input ~ nav > ul > li {
      text-align: center;
      opacity: 0;
      -webkit-transition: .5s;
      transition: .5s;
      -webkit-transition-delay: 0s;
              transition-delay: 0s; }
      .header-menu input ~ nav > ul > li > a {
        text-decoration: none;
        text-transform: uppercase;
        color: black;
        font-weight: 700;
        font-family: sans-serif;
        display: block;
        padding: 30px; }

.header-menu input:checked ~ nav {
  height: 100%;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  background-color: #715dff; }
  .header-menu input:checked ~ nav > ul > li {
    opacity: 1;
    -webkit-transition-delay: .5s;
            transition-delay: .5s; }

